    monospace;
}

.container .panel .main{font-size:20px}.container .panel .main .divider{width:5px;height:16px}.container .panel .main .controls li{padding:5px 0 19px 0px;line-height:120%}.container .panel .main .controls li a{background:none;border:none;padding:0;font-family:arial,sans-serif;color:#000;text-decoration:none;cursor:pointer}.container .panel .main .controls li a:hover{color:#fff;text-shadow:0 0 10px #000,0 0 20px #000;color:var(--primary-text-highlight);-webkit-transform:scale3d(1.05, 1.05, 1.05);transform:scale3d(1.05, 1.05, 1.05)}.container .panel .main .game-buttons{padding:8px;box-sizing:border-box;border-radius:8px}.container .panel .main .game-buttons button{font-size:24px;padding-left:12px;padding-right:12px;padding:12px;width:100%;background-color:#6495ed;border-color:#6495ed;border-width:4px;border-style:solid;box-sizing:border-box;cursor:pointer;border-radius:12%/50%}.container .panel .main .game-buttons button:hover{color:#12274d;background-color:#cddcf3}
